@media only screen and (max-width: 576px) {
    .sternet-section {
        height: auto!important;
    }
    .light-images,
    .sternet-text {
        position: relative!important;
        top: auto!important;
        left: auto!important;
        transform: translate(0%, 0%)!important;
    }
    .direction_fl {
        flex-direction: column-reverse!important;
    }
    .sternet-card {
        margin: 30px 0px;
    }
    .light-box {
        width: auto!important;
    }
    .sternet-text h3 {
        font-size: 22px;
    }
    .sternet-text p {
        font-size: 16px;
    }
}